import { filter, fromEventPattern, share, take, takeUntil, tap } from "rxjs";
import Browser from "webextension-polyfill";
export class PortStream {
    constructor(port) {
        this._port = port;
        this._disconnectStream = fromEventPattern((h) => this._port.onDisconnect.addListener(h), (h) => this._port.onDisconnect.removeListener(h)).pipe(take(1), tap(() => (this._connected = false)), share());
        this._messagesStream = fromEventPattern((h) => this._port.onMessage.addListener(h), (h) => this._port.onMessage.removeListener(h), (msg) => msg).pipe(share(), takeUntil(this._disconnectStream));
        this._connected = true;
    }
    static connectToBackgroundService(name) {
        return new PortStream(Browser.runtime.connect({ name }));
    }
    get onMessage() {
        return this._messagesStream;
    }
    get onDisconnect() {
        return this._disconnectStream;
    }
    get connected() {
        return this._connected;
    }
    sendMessage(msg) {
        if (!this._port) {
            throw new Error("Port to background service worker is not defined");
        }
        this._port.postMessage(msg);
        return this.createResponseObservable(msg.id);
    }
    createResponseObservable(requestMsgID) {
        return this._messagesStream.pipe(filter((msg) => msg.id === requestMsgID));
    }
}
